.container {
    padding: 2em;
}

.title {
    text-align: center;
}
.flex {
    display: flex;
    flex-direction: row-reverse;
}

.flex > * {
    flex: 1;
}

.column {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media only screen and (max-width: 768px) {
    .container {
        display: block;
    }
}