.container {
    display: flex;
    flex-direction: row-reverse;
    margin: 2em;
}

.textContainer {
    padding: 0 4em;
    min-width: 200px;
}

.title {
    font-size: 28px;
    margin-bottom: 2em;
}

@media only screen and (max-width: 768px) {
    .container {
        display: block;
    }

    .textContainer {
        min-width: 0;
        padding: 0;
    }
}