.top {
    position: absolute;
    top: 0;
    width: 100%;
}

.container {
    width: 100%;
    height: 100px;
    position: absolute;
    top: 0;
    z-index: 3;
}

.container .menu {
    height: inherit;
    background-color: #10110c;
    width: 80px;
    position: absolute;
    z-index: 3;
}

.container img {
    height: 80px;
}

.menuLink, .menuLinkHide {
    height: 20px;
    color: #fff;
    transform: translate(0, -16px);
}

.menuLink div {
    width: calc(80px - 2em);
    height: 3px;
    margin: 5px auto;
    background-color: #fff;
    border-radius: 4px;
    transition: width 1s ease-in-out, transform 2s ease-in-out;
    transform: scaleX(1);
}

.menuLinkHide div {
    width: 3px;
    height: 3px;
    margin: 5px auto;
    background-color: #fff;
    border-radius: 4px;
    transition: width 1.5s ease-in-out, transform 2s ease-in-out;
    transform: scaleX(1);
}

.navMobile {
    position: absolute;
    list-style: none;
    background-color: #10110c;
    top: 0;
    z-index: 0;
    display: flex;
    height: calc(50vh - 100px);
    width: 80px;
    color: #fff;
    justify-content: space-evenly;
    transform: translateY(-100%);
    transition: transform 1s ease-in-out;
    flex-direction: column;
}

.navMobileShow {
    display: block;
    position: absolute;
    list-style: none;
    background-color: #10110c;
    top: 0;
    z-index: 0;
    display: flex;
    height: calc(50vh - 100px);
    width: 80px;
    color: #fff;
    justify-content: space-evenly;
    transform: translateY(100px);
    transition: transform 2s ease-in-out;
    flex-direction: column;
}

.navMobile li,
.navMobileShow li {
    padding: 0 1em;
    font-size: 12px;
}

.navMobile li a,
.navMobileShow li a {
    text-decoration: none;
    color: #fff;
}

.navMobile li div, .navMobileShow li div {
    display: flex;
    flex-direction: column;
}

.navMobile li div span, .navMobileShow li div span {
    padding: 0.5em 0;
}
