.container {
    width: 75%;
    margin: auto;
    padding: 3em;
    display: grid;
    grid-template-rows: 50px 1fr 50px 1fr 1fr 75px 75px;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    gap: 16px;
    height: 100%;
    border: 1px solid #9e9e9e;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

form p { 
    margin: 0;
}

input:not(input[type="checkbox"]), select, button[type="submit"], textarea {
    width: calc(100% - 2em);
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
    border: 0;
    height: 40px;
    padding: 0 1em;
}

input[type="checkbox"] {
    transform: translate(5px, 2px);
}

input[type="date"] {
    margin-top: 1em;
}

textarea {
    height: 100%;
    padding: 1em 0 0 1em;
}

select {
    margin-top: 1em;
}

.name {
    grid-row-start: 1;
    grid-column-start: 1;
    grid-row-end: 2;
    grid-column-end: 3;
}
.email {
    grid-row-start: 1;
    grid-column-start: 3;
    grid-row-end: 2;
    grid-column-end: 5;
}
.tel {
    grid-row-start: 1;
    grid-column-start: 5;
    grid-row-end: 2;
    grid-column-end: 7;
}
.note {
    grid-row-start: 2;
    grid-column-start: 1;
    grid-row-end: 3;
    grid-column-end: 7;
}
.shot {
    grid-row-start: 3;
    grid-column-start: 1;
    grid-row-end: 4;
    grid-column-end: 7;
 }
.checkin {
    grid-row-start: 4;
    grid-column-start: 1;
    grid-row-end: 5;
    grid-column-end: 4;
 }
 .checkout {
    grid-row-start: 4;
    grid-column-start: 4;
    grid-row-end: 5;
    grid-column-end: 7;
 }
 .room {
    grid-row-start: 5;
    grid-column-start: 1;
    grid-row-end: 6;
    grid-column-end: 3;
 }
 .breakfast {
    grid-row-start: 5;
    grid-column-start: 3;
    grid-row-end: 6;
    grid-column-end: 5;
    max-width: 150px;
 }
 .car {
    grid-row-start: 5;
    grid-column-start: 5;
    grid-row-end: 6;
    grid-column-end: 7;
 }

 button[type="submit"] {
    grid-row-start: 7;
    grid-column-start: 1;
    grid-row-end: 8;
    grid-column-end: 7;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
 }

 .label > p {
    margin: 0;
 }
.captcha {
    grid-row-start: 6;
    grid-column-start: 1;
    grid-row-end: 7;
    grid-column-end: 7;
}
 .checkin > label > p, checkout > label > p, .breakfast > div > label > p {
    margin: .25em;
 }
 
 .breakfast > div > label  {
    display: flex;
    justify-content: space-between;
 }
 
 .breakfast > div  {
    margin-top: .5em;
 }

 .breakfast > p  {
    margin: 0;
 }

 .breakfast > div > label > input[type="checkbox"] {
    transform: translate(0, 0);
 }

 .disabled {
    color: #9e9e9e;
 }

 @media only screen and (max-width: 768px) {
    .container {
        display: block;
    }

    .container * {
        margin: 1em 0;
    }

    .breakfast {
        max-width: 100%;
    }

    .car {
        display: flex;
        justify-content: space-between;
    }

    .car input[type="checkbox"] {
        transform: translate(0, 0);
    }
}