.breakfast {
    margin: 0 1em;
}

.container {
    display: flex;
    flex-direction: row;
    margin: 2em;
}

.textContainer {
    padding: 0 4em;
    min-width: 200px;
}

.title {
    font-size: 28px;
    margin-bottom: 2em;
}

@media only screen and (max-width: 768px) {
    .container {
        display: block;
    }
    .textContainer {
        min-width: 0;
        padding: 0;
    }
    .breakfast {
        margin: 0 30px;
    }

    .flex {
        flex-direction: column-reverse;
    }
}