.footer {
    background-color: #f0f0f0;
    color: #000;
    height: auto;
    margin-top: 150px;
    padding: 2em 1em;
}

.container {
    display: flex;
    align-content: stretch;
    justify-content: space-evenly;
    height: inherit;
}

.container p {
    align-self: flex-start;
}

.container a {
    color: #000;
    text-decoration: none;
}

@media only screen and (max-width: 768px) {
    .container {
        display: block;
    }
}