.title {
    margin: 0 2em;
}

.container {
    display: grid;
    grid-template-columns: 50% 50%;
    padding: 0 3em;
}

.flex {
    display: flex;
    justify-content: space-between;
}

.flex ul {
    padding: 0 10px;
}

.link {
    margin: 0 2em;
}

ul {
    list-style: none;
    padding: 1em 0;
    margin-right: 1em;
}

ul li {
    margin: 1em 0;
}

ul li > div {
    border-bottom: 1px solid #000;
    line-height: 3;
}

@media only screen and (max-width: 768px) {

    .container {
        display: block;
    }
}