.container {
    margin: 2em 0;
}

.backgroundImage {
    height: 400px;
    position: relative;
    box-shadow: 0 30px 10px -20px rgba(0,0,0,0.45) inset, 0 -30px 10px -20px rgba(0,0,0,0.45) inset;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.backgroundImage > * {
    color: #000;
    padding: 1em 2em;
    text-decoration: none;
    }

.backgroundImage span {
    background-color: #fff;
    padding: 1em 3em;
    color: #000;
}

.backgroundImage span:hover {
    background-color: #000;
    padding: 1em 3em;
    color: #fff;
    transition: all 1s ease-in-out;
}

.backgroundImage a {
  --b: 2px;   /* border thickness */
  --s: 1.75em; /* size of the corner */
  --color: #fff;
  color: #000;
  
  padding: calc(.5em + var(--s)) calc(.9em + var(--s));
  color: var(--color);
  --_p: var(--s);
  background:
    conic-gradient(from 90deg at var(--b) var(--b),#0000 90deg,var(--color) 0)
    var(--_p) var(--_p)/calc(100% - var(--b) - 2*var(--_p)) calc(100% - var(--b) - 2*var(--_p));
  transition: .3s linear, color 0s, background-color 0s;
  outline: var(--b) solid #0000;
  outline-offset: .6em;
  font-size: 22px;
    text-decoration: none;
  border: 0;

  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.backgroundImage a:hover,
.backgroundImage a:focus-visible{
  --_p: 0px;
  outline-color: var(--color);
  outline-offset: .05em;

}

.backgroundImage  a:active {
  background: var(--color);
  color: #fff;
}

@media only screen and (max-width: 768px) {
    .backgroundImage {
        height: 50vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .backgroundImage > * {
        position: relative;
        right: 0;
        bottom: 0
    }
}