header {
    margin-bottom: 100px;
    background-color: #fff;
}

.banner {
    height: calc(100vh - 116px);
    position: relative;
}

.banner::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #000;
    opacity: 0.3;
}

.button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    padding: 1em 2em;
    background-color: transparent;
    color: #fff;
    font-size: 20px;
    border: solid 2px #fff;
    transition: all 1s ease-in-out;
    z-index: 2;
    text-decoration: none;
}

.button:hover {
    background-color: #fff;
    color: #000;
}

h1 {
    text-align: center;
    margin: 1em;
    font-size: 7vw;
    line-height: 1;
    color: #fff;
    mix-blend-mode: difference;
}

@media only screen and (max-width: 768px) {

    .banner {
        height: 50vh;
    }
    h1 {
        font-size: 15vw;
        line-height: 1;
        margin: 1em;
    }
}