.nav {
    display: flex;
    align-content: space-around;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    padding: 1em 2em;
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}



.sticky {
    position: -webkit-sticky;
    position: sticky;
    z-index: 3;
    top: 0;
}

.nav * {
    flex: 1 1 0px;
    text-align: center;
    color: #000;
    text-decoration: none;
}

.link span {
    padding: 0 5px;
}

.line {
    width: 50px;
    height: 1px;
    background-color: #000;
    flex-shrink: 0;
    flex-grow: 1;
    padding: 0 20px;
}

.logo {
    width: 80px;
    height: 80px;
}